}
.slick-next {
  top: 213px;
  left: -50px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
